import CommonHeader from '@/components/layouts/CommonHeader.vue'
import graceFullLoading from "@/graceUI/components/graceFullLoading.vue"

export default {
	components: {
		CommonHeader,
		graceFullLoading
	},
	data() {
		return {
			loading: true,
			info: {},
			list: []
		}
	},
	onLoad(e) {
		this.getInfo(e.id)
		this.getList(e.id)
	},
	methods: {
		toProduct(id) {
			uni.navigateTo({
				url: "/pages/product/product_info?id=" + id
			})
		},
		getList(id) {
			uni.request({
				url: getApp().globalData.api + 'store/product-list',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: id
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.list = res.data.data
				},
				fail: (res) => {

				}
			});
		},
		collect() {
			uni.request({
				url: getApp().globalData.api + 'store/collect',
				method: 'POST',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: this.info.id,
					status: this.info.is_collect == 1 ? 0 : 1
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					if (this.info.is_collect == 1) {
						this.info.is_collect = 0
						this.info.collect -= 1
					} else {
						this.info.is_collect = 1
						this.info.collect += 1
					}
				},
				fail: (res) => {

				}
			});
		},
		getInfo(id) {
			uni.request({
				url: getApp().globalData.api + 'store/info',
				method: 'GET',
				data: {
					open_id: uni.getStorageSync('open_id'),
					id: id
				},
				header: {
					'content-type': 'application/json' //自定义请求头信息
				},
				success: (res) => {
					this.info = res.data.data
					this.loading = false
				},
				fail: (res) => {
					this.loading = false
				}
			});
		}
	}

}
